<template>
    <div class="tagging">
        <navigate title="我的上报" position="absolute"/>
        <div class="scroll-wrapper" ref="scrollWrapper">
            <mu-load-more v-if="list.length" :refreshing="refresh" :loading="loading"
                          @refresh="onRefresh"
                          @load="onLoad">
                <mu-list class="pn">
                    <swipe-cell class="border-1px-b" v-for="(item, index) in list" :key="item.id" :right-width="120">
                        <mu-list-item @click.native="handlerItem(item)" avatar>
                            <!--<mu-list-item-action>-->
                                <!--<mu-avatar :color="item.shared   ? '#ff9800':'#2196f3'">-->
                                    <!--<img class="db p-10" :src="icon">-->
                                <!--</mu-avatar>-->
                            <!--</mu-list-item-action>-->
                            <mu-list-item-content class="tagging-content">
                                <mu-list-item-title>{{item.type.name}}</mu-list-item-title>
                                <mu-list-item-sub-title>
                                    <span v-if="item.status=='032001'"> 描述：{{item.describe}}</span>
                                    <span v-if="item.status=='032002'" style="color: #13ce66">已确认</span>

                                </mu-list-item-sub-title>
                            </mu-list-item-content>
                            <!--<mu-list-item-action>-->
                                <!--<div class="fz-14">{{item.shared ? '共享':'私有'}}</div>-->
                            <!--</mu-list-item-action>-->
                        </mu-list-item>
                        <div class="flex h" slot="right">
                            <mu-button class="flex-1 h bdrsn modify"  color="#fff" @click="handlerSave(item, index)"
                                       flat>
                                修改
                            </mu-button>
                            <mu-button class="flex-1 h bdrsn delete" color="#fff" @click="handlerDelete(item, index)"
                                       flat>
                                删除
                            </mu-button>
                        </div>
                    </swipe-cell>
                </mu-list>
            </mu-load-more>
            <no-success text="您还没有任何上报信息" v-else/>
        </div>
        <transition name="fade-left">
            <keep-alive>
                <router-view class="views" @update="onRefresh"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import NoSuccess from "../../../base/no-success/no-success"
    import SwipeCell from 'vant/lib/swipe-cell'
    import 'vant/lib/swipe-cell/style'
    import {queryReport, deleteReportInfo} from "../../../api/personal"
    import {MapMixins} from "../../../common/js/mixins"
    import {isArray} from "../../../types"

    export default {
        name: "tagging",
        mixins: [MapMixins],
        data() {
            return {
                icon: 'static/icon/search_label.png',
                refresh: false,
                loading: false,
                params: {
                    userId: "111",
                    pageNumber: 0,
                    pageSize: 10
                },
                list: []
            }
        },
        activated() {
            this.queryReport()
        },
        methods: {
            // 点击选项
            handlerItem(item) {
                this.$toast({
                    message: '定位中 ...',
                    duration: 0,
                })
                this.setCenter(item.longitude, item.latitude).then(res => {
                    this.$toast('定位成功')

                    this.$router.push('/')
                }).catch(err => {
                    this.$toast('定位失败')
                })
            },
            // 下拉刷新
            onRefresh() {
                this.refresh = true
                this.$refs.scrollWrapper.scrollTop = 0
                this.queryReport()
            },
            // 上拉加载
            onLoad() {
                this.loading = true
                this.params.pageNumber++

                queryReport(this.params).then(res => {
                    this.list.push(...res)
                    this.loading = false
                })
            },
            // 搜索
            queryReport() {
                this.params.pageNumber = 0

                queryReport(this.params).then(res => {
                    this.list = isArray(res)
                    this.refresh = false
                })
            },
            // 删除
            handlerDelete(item, index) {
                deleteReportInfo(item.id).then(res => {
                    this.list.splice(index, 1)

                    window.Arcgis && window.Arcgis.refreshMapLayers()
                })
            },
            /***
             * 点击进入修改标注信息界面
             * @param item
             * @param index
             */
            handlerSave(item, index) {
                this.$router.push({
                    name: 'about-report-detail',
                    params: {
                        id: item.id
                    }
                })
            }
        },
        components: {
            NoSuccess,
            Navigate,
            SwipeCell
        }
    }
</script>

<style lang="stylus">
    .tagging
        padding-top 46px
</style>



